.advisorprofile-main{
    background-color: #fafafa;
    margin: 3vw;
}
.advisorprofile-card,
.btn-container{
    padding: 20px 20px;
    border-radius: 5px;
    background-color: #fff;
    box-shadow:  1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.contact-detail-form{
    display: flex;
    flex-wrap: wrap;
    gap:0 10px;
}